import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { EditLeaveemployeeComponent } from '../edit-leaveemployee/edit-leaveemployee.component';
import { AddLeaveemployeeComponent } from '../add-leaveemployee/add-leaveemployee.component';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { TitleCasePipe } from '@angular/common';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { EmpleaveserviceService } from 'src/app/core/services/empleaveservice.service';

@Component({
  selector: 'app-leave-employee',
  templateUrl: './leave-employee.component.html',
  styleUrls: ['./leave-employee.component.scss']
})
export class LeaveEmployeeComponent implements OnInit {

  public currentEmployee = JSON.parse(localStorage.getItem("currentUser"));
  public userId: any;
  public leaveAllData: any;
  public leaveId: any;
  public thismonth: any;
  public totalLeavesAvailable: any;
  public totalLeavesTakenCombined: any;
  public sickleavedata: any;
  public casualleavedata: any;
  public privilageleavedata: any;
  public compensatoryleavedata: any;


  constructor(public dialog: MatDialog, public leaveservice: EmpleaveserviceService,
    public rolesPermissionsService: RolesPermissionsService,
    private titleCasePipe: TitleCasePipe, private toastService: ToastNotificationService,) { }

  ngOnInit(): void {

    console.log("employee ", this.currentEmployee, "employee Id", this.currentEmployee.id)

    this.getEmployeeByEmployeeId();
    this.getleaveDetailsdays();
    this.getleaveDetails();
    //this.getleaveDays();
    this.leaveservice.leaveAdded$.subscribe(() => {
      // Fetch the updated list of users
      this.getleaveDetails();
    });
  }

  getEmployeeByEmployeeId() {
    this.rolesPermissionsService.getEmployeeById({}, this.currentEmployee.id).then((res) => {

      //console.log("In Leave --> employee BY UDER Id", res.data);
      this.userId = res.data.parentId;
    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

  getleaveDetails() {
    this.leaveservice.getAllLeaveDetails({}, this.currentEmployee.id).then((res) => {
      //console.log("res LEAVE LIST", res);
      this.leaveAllData = res.data;

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  getleaveDetailsdays() {
    this.leaveservice.getAllLeaveDetails1({}, this.currentEmployee.id).then((res) => {
      console.log("res days Assign", res);
      this.totalLeavesAvailable = res.data.totalLeavesAvailable;
      console.log("this.totalLeavesAvailable", this.totalLeavesAvailable)
      this.totalLeavesTakenCombined = res.data.totalLeavesTakenCombined;
      console.log("this.totalLeavesTakenCombined", this.totalLeavesTakenCombined)
      this.sickleavedata = res.data.totalLeaveTaken["Sick Leave"];
      this.casualleavedata = res.data.totalLeaveTaken["Casual Leave"];
      this.privilageleavedata = res.data.totalLeaveTaken["Privilage Leave"];
      this.compensatoryleavedata = res.data.totalLeaveTaken["Compensatory Leave"];
      this.thismonth = res.data.monthlyHoliday[0].count == null ? 0 : res.data.monthlyHoliday[0].count ;


    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message)
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  openAddLeave(userId) {
    const dialogRef = this.dialog.open(AddLeaveemployeeComponent, {
      width: '700px',
      height: '730px',
      data: { userId: userId },
      panelClass: 'custom-dialog-AddLeave-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {

    });

  }

  openEditLeave(leaveId, userId) {
    const dialogRef = this.dialog.open(EditLeaveemployeeComponent, {
      width: '700px',
      height: '730px',
      data: { leaveId: leaveId, userId: userId },
      panelClass: 'custom-dialog-updateLeave-class',
      disableClose: true,
      position: {
        right: '0',
      },

    });

    dialogRef.afterClosed().subscribe(result => {

    });
  }

  getleaveId(leaveId) {
    this.leaveId = leaveId

  }
  deletleaveById() {
    this.leaveservice.deleteLeave({}, this.leaveId).then(res => {
      // this.toastService.openSnackBar(" Leave Deleted Successfully")
      this.toastService.toastMsg({
        title: "Success",
        content: "Leave Deleted Successfully!!!",
      });
      this.getleaveDetails();

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong."
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })

  }

}
