import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { UrlConstants } from 'src/app/_helpers/url-constants';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class EmpleaveserviceService {

  private leaveAddedSource = new Subject<void>();
  leaveAdded$ = this.leaveAddedSource.asObservable();

  notifyLeaveAdded() {
    this.leaveAddedSource.next();
  }

  constructor(public httpClient:HttpService ) { }

  getAllLeaveDetails(data:{},empId):any{
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgetAllleave + empId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  
  getAllLeaveDetails1(data:{},empId):any{
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgetAllleave1 + empId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }


  addLeaveDetails(data:{}){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empaddleave ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  GetLeaveByEducationId(data:{},leaveId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empgetByIdleave + leaveId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
   
  }

  LeaveUpdateByEmployeeId(data:{},empId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empupdateByIdleave + empId ,'PUT').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  deleteLeave(data:{},leaveId){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.empdeleteByIdleave + leaveId ,  'DELETE').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
}
