import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainleaveEmployeeComponent } from './mainleave-employee/mainleave-employee.component';
import { LeaveEmployeeComponent } from './leave-employee/leave-employee.component';

const routes: Routes = [
  {
    path:'',
    component:MainleaveEmployeeComponent,
    children: [
      {
        path:'',
        redirectTo: 'emp-leave',
        pathMatch: 'full'
      },
      {
        path:'emp-leave',
        component:LeaveEmployeeComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeLeaveRoutingModule { }
